.headerContainer{
    display: flex;
    align-items: center;
    height: 70px;
    background-color: #fff;
    width: 100%;
    justify-content: space-between;
    border-bottom: .5px solid #aaa;
    padding: 0px 16px;
}
.headerContainerLeft {
    display: flex;
    background-color: transparent;
    width: 60%;
}
.headerContainerLeft img {
    margin-right: 77px;
}


.headerContainerRight {
    display: flex;
    width: max-content;
}