@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

.buttonHeader {
    background-color: transparent;
    outline: none;
    height: 70px;
    border: none;
    width: max-content;
    padding: 0px 20px;
    margin: 0 5px;
    border-bottom: 4px solid #d0021b;
    cursor: pointer;
    color: #d0021b;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
}